body{
    background-color: lightgray;
}
.btn{
    background-color: rgb(0, 56, 0);
    transition: .2s ease-in-out;
    border: 1.5px solid rgb(0, 56, 0);
    color: white;
    box-shadow: none;
}
.btn:hover, .btn:focus{
    background-color: rgb(5, 46, 5);
    border: 1.5px solid rgb(5, 46, 5);
    box-shadow: none;
}

.modal {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
}
.modal-content {
    background-color: #fff;
    color: #000000;
    padding: 50px;
    border-radius: 8px;
    text-align: center;
    width: 300px;
}
.modal-content h3 {
    margin: 0 0 10px;
}
.circle-container {
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.circle-label {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid transparent;
    transition: border-color 0.3s ease;
}
.circle-label.default {
    background-color: lightgray;
}
.circle-label.circle1 {
    background-color: lightgreen;
}
.circle-label.circle2 {
    background-color: lightcoral;
}
.circle-label.circle3 {
    background-color: lightblue;
}
input[type="radio"] {
    display: none;
}
input[type="radio"]:checked + .circle-label {
    border-color: rgb(92, 91, 91);
}
